.gallery {
  background-color: white;
  display: grid;
  grid-template: repeat(7, 5vw) / repeat(8, 1fr);
  grid-gap: 0.5rem;
  padding: 0.5rem;
  position: relative;
}

figure {
  margin-bottom: 0;
}

.gallery__item--1 {
  grid-row: 1 / span 2;
  grid-column: 1 / span 2;
}
.gallery__item--2 {
  grid-row: 1 / span 3;
  grid-column: 3 / span 3;
}
.gallery__item--3 {
  grid-row: 1 / span 2;
  grid-column: 6 / 7;
}
.gallery__item--4 {
  grid-row: 1 / span 2;
  grid-column: 7 / -1;
}
.gallery__item--5 {
  grid-row: 3 / span 3;
  grid-column: 1 / span 2;
}
.gallery__item--6 {
  grid-row: 4 / span 2;
  grid-column: 3 / span 2;
}
.gallery__item--7 {
  grid-row: 4 / 5;
  grid-column: 5 / 6;
}
.gallery__item--8 {
  grid-row: 3 / span 2;
  grid-column: 6 / span 2;
}
.gallery__item--9 {
  grid-row: 3 / span 3;
  grid-column: 8 / -1;
}
.gallery__item--10 {
  grid-row: 6 / span 2;
  grid-column: 1 / 2;
}
.gallery__item--11 {
  grid-row: 6 / span 2;
  grid-column: 2 / span 2;
}
.gallery__item--12 {
  grid-row: 6 / span 2;
  grid-column: 4 / 5;
}
.gallery__item--13 {
  grid-row: 5 / span 3;
  grid-column: 5 / span 3;
}
.gallery__item--14 {
  grid-row: 6 / span 2;
  grid-column: 8 / -1;
}

.gallery__img {
  width: 100%;
  height: 100%;

  object-fit: cover;
  object-position: center;
  display: block;
}

.gallery__item--6 .gallery__img {
  object-position: top;
  -webkit-transform: scaleX(-1);
          transform: scaleX(-1);
}

.welcome-box {
  z-index: 1;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  text-align: center;
  background: rgba(0, 0, 0, 0.8);
  padding: 20px;
  width: 500px;
}

.welcome-box h2,
.welcome-box h1 {
  color: var(--light);
  line-height: 4rem;
  letter-spacing: 4px;
}

.welcome-box h2 {
  font-weight: 700;
}

.welcome-box h1 {
  font-weight: 300;
  font-size: 50px;
}

@media (max-width: 1000px) {
  .welcome-box {
    width: 45%;
    padding: 15px;
  }
  .welcome-box h2,
  .welcome-box h1 {
    font-size: 24px;
    line-height: 3rem;
    letter-spacing: 3px;
  }
}

@media (max-width: 900px) {
  .welcome-box {
    width: 40%;
    padding: 12px;
  }
  .welcome-box h2,
  .welcome-box h1 {
    font-size: 20px;
    line-height: 2rem;
    letter-spacing: 2px;
  }
}

@media (max-width: 580px) {
  .welcome-box {
    width: 35%;
    padding: 10px;
  }
  .welcome-box h2,
  .welcome-box h1 {
    font-size: 16px;
    line-height: 1.5rem;
    letter-spacing: 1.5px;
  }
}

@media (max-width: 500px) {
  .welcome-box {
    width: 30%;
    padding: 7px 10px;
  }
  .welcome-box h2,
  .welcome-box h1 {
    font-size: 15px;
    line-height: 1.3rem;
    letter-spacing: 1.5px;
  }
}

